@import url('https://fonts.googleapis.com/css?family=DM+Sans:500,700&display=swap');

@media (min-width: 768px) {
    .insertBody {
        margin: 0 auto;
        width: 375px;
        background-color: #fff8fb;
    }
}
.insertBody {
    background-color: #fff8fb;
    overflow: hidden;
    position: relative;
    overflow-x: hidden;
}

.schedule_nav {
    box-sizing: border-box;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    position: relative;
    overflow: hidden;
    max-width: 340px;
    height: 45px;
    /* background-color: #fff; */
    padding: 0 10px;
    border: none;
    /* border-radius: 40px; */
    width: 100%;
    overflow: hidden;
    /* box-shadow: 0 0px 5px rgba(159, 162, 177, 0.8); */
}

.nav-item {
    color: #83818c;
    padding: 20px;
    padding-top: 0;
    text-decoration: none;
    transition: 0.3s;
    z-index: 1;
    font-family: 'DM Sans', sans-serif;
    font-weight: 500;
    font-size: 90%;
    position: relative;

    &:before {
        content: '';
        position: absolute;
        top: 38px;
        left: 0;
        width: 100%;
        height: 2px;
        background-color: #dfe2ea;
        /* border-radius: 8px 8px 0 0; */
        opacity: 0;
        transition: 0.3s;
    }
}

.nav-item:not(.is-active):hover:before {
    opacity: 1;
    bottom: 0;
}

.nav-item:not(.is-active):hover {
    color: #333;
}

.nav-indicator {
    position: absolute;
    left: 0;
    bottom: 0;
    height: 2px;
    transition: 0.4s;
    height: 2px;
    z-index: 1;
    /* border-radius: 8px 8px 0 0; */
}

@media (max-width: 580px) {
    .schedule_nav {
        overflow: hidden;
    }
}
